import React, { useState, useEffect } from "react";
import FavoritesTab from "./tabs/FavoritesTab";
import MyRecipeTab from "./tabs/MyRecipeTab";
import FeedsTab from "./tabs/FeedsTab";
import CommentsTab from "./tabs/CommentsTab";
import style from "../../styles/myPage/MyPage.module.css";

const MyPage = () => {
  const [activeTab, setActiveTab] = useState("favorites");
  const [nickname, setNickname] = useState("");
  const [newNickname, setNewNickname] = useState("");
  const [isEditing, setIsEditing] = useState(false);

  useEffect(() => {
    const fetchUserNickname = async () => {
      try {
        const token = localStorage.getItem("token"); //  
        if (!token) {
          console.error(" .");
          return;
        }

        const userId = JSON.parse(atob(token.split(".")[1])).userid; //  userid 
        console.log(" userId:", userId);

        const response = await fetch(`http://localhost:8080/user/${userId}`, {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
        });

        if (!response.ok) {
          console.error(" :", response.status, response.statusText);
          throw new Error("    .");
        }

        const data = await response.json();
        console.log(" :", data);
        setNickname(data.nickname);
      } catch (error) {
        console.error("     :", error);
      }
    };

    fetchUserNickname();
  }, []);

  const handleEditClick = () => {
    setIsEditing(true);
    setNewNickname(nickname);
  };

  const handleSaveClick = async () => {
    try {
      const token = localStorage.getItem("token");
      if (!token) {
        console.error(" .");
        return;
      }

      const userId = JSON.parse(atob(token.split(".")[1])).userid;
      const response = await fetch(
        `http://localhost:8080/user/${userId}/nickname`,
        {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify({ nickname: newNickname }),
        }
      );

      if (!response.ok) {
        console.error(" :", response.status, response.statusText);
        throw new Error("  .");
      }

      const data = await response.json();
      console.log("  :", data);
      setNickname(data.nickname);
      setIsEditing(false);
    } catch (error) {
      console.error("    :", error);
    }
  };

  const handleCancelClick = () => {
    setIsEditing(false);
  };

  const renderTabContent = () => {
    switch (activeTab) {
      case "favorites":
        return <FavoritesTab />;
      case "myRecipes":
        return <MyRecipeTab />;
      case "feeds":
        return <FeedsTab />;
      case "comments":
        return <CommentsTab />;
      default:
        return null;
    }
  };

  return (
    <main className={`mw ${style.main}`}>
      <h2>{nickname} </h2>
      <div className={style.profile}>
        <img src="./img/logo.png" alt="Profile" />
        {isEditing ? (
          <>
            <input
              type="text"
              value={newNickname}
              onChange={(e) => setNewNickname(e.target.value)}
              className={style.input}
            />
            <button onClick={handleSaveClick} className={style.button}>
              
            </button>
            <button onClick={handleCancelClick} className={style.button}>
              
            </button>
          </>
        ) : (
          <button onClick={handleEditClick} className={style.button}>
             
          </button>
        )}
      </div>
      <div className={style.tabs}>
        <button
          className={activeTab === "favorites" ? style.active : ""}
          onClick={() => setActiveTab("favorites")}
        >
          
        </button>
        <button
          className={activeTab === "myRecipes" ? style.active : ""}
          onClick={() => setActiveTab("myRecipes")}
        >
           
        </button>
        <button
          className={activeTab === "feeds" ? style.active : ""}
          onClick={() => setActiveTab("feeds")}
        >
           
        </button>
        <button
          className={activeTab === "comments" ? style.active : ""}
          onClick={() => setActiveTab("comments")}
        >
           
        </button>
      </div>
      <div className={style.tabContent}>{renderTabContent()}</div>
    </main>
  );
};

export default MyPage;
