import React, { useState } from 'react';

// Translate API  
const translateText = async (text, targetLanguage = 'en') => {
    const apiKey = process.env.REACT_APP_TRANSLATE_API_KEY; //  API  .
    const url = `https://translation.googleapis.com/language/translate/v2?key=${apiKey}`;

    const response = await fetch(url, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            q: text,
            target: targetLanguage,
        }),
    });

    const data = await response.json();
    return data.data.translations[0].translatedText;
};

const SearchBar = ({ onSearch }) => {
    const [searchTerm, setSearchTerm] = useState('');
    const [searchType, setSearchType] = useState('s');

    const handleSearch = async () => {
        if (!searchTerm.trim()) {
            alert(' .');
            return;
        }

        try {
            const translatedTerm = await translateText(searchTerm);
            onSearch(translatedTerm, searchType);
        } catch (error) {
            console.error('   :', error);
            alert('  .   .');
        }
    };

    const handleKeyPress = (e) => {
        if (e.key === 'Enter') {
            handleSearch();
        }
    };

    return (
        <div>
            <div>
                <label>
                    <input
                        type="radio"
                        name="searchType"
                        value="s"
                        checked={searchType === 's'}
                        onChange={() => setSearchType('s')}
                    />
                     
                </label>
                <label>
                    <input
                        type="radio"
                        name="searchType"
                        value="i"
                        checked={searchType === 'i'}
                        onChange={() => setSearchType('i')}
                    />
                     
                </label>
            </div>
            <div>
                <input
                    type="text"
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    onKeyPress={handleKeyPress}
                    placeholder=" "
                />
                <button onClick={handleSearch}>
                    <i className="fa-solid fa-magnifying-glass"></i>
                </button>
            </div>
        </div>
    );
};

export default SearchBar;
