import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import style from '../../styles/feed/CreateFeed.module.css';

const FeedEdit = () => {
  const { id } = useParams();
  const [title, setTitle] = useState('');
  const [imgFile, setImgFile] = useState(null);
  const [imgPreviewUrl, setImgPreviewUrl] = useState('');
  const [content, setContent] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    const fetchFeed = async () => {
      try {
        const token = localStorage.getItem('token');
        const response = await fetch(`http://localhost:8080/feedDetail/${id}`, {
          method: 'GET',
          headers: {
            Authorization: `Bearer ${token}`,
          },
          credentials: 'include',
        });

        if (response.ok) {
          const data = await response.json();
          setTitle(data.title);
          setImgPreviewUrl(data.imgUrl); //  URL   
          setContent(data.content);
        } else {
          throw new Error('   .');
        }
      } catch (error) {
        console.error('Error fetching feed:', error);
        alert('   .');
      }
    };

    fetchFeed();
  }, [id]);

  const handleImageUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      setImgFile(file);

      //   
      const reader = new FileReader();
      reader.onloadend = () => {
        setImgPreviewUrl(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleImageDelete = () => {
    setImgFile(null);
    setImgPreviewUrl(''); //   
  };

  const updateFeed = async (e) => {
    e.preventDefault();

    if (title === '') {
      alert(' ');
      document.getElementById('title').focus();
      return;
    }
    if (!imgFile && !imgPreviewUrl) {
      alert(' ');
      document.getElementById('imgFile').focus();
      return;
    }
    if (content === '') {
      alert(' ');
      document.getElementById('content').focus();
      return;
    }

    const formData = new FormData();
    formData.append('title', title);
    formData.append('imgFile', imgFile);
    formData.append('content', content);

    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`http://localhost:8080/feedEdit/${id}`, {
        method: 'PUT', //  'PATCH' 
        headers: {
          Authorization: `Bearer ${token}`,
        },
        body: formData,
        credentials: 'include',
      });

      if (response.ok) {
        alert('  .');
        setTitle('');
        setImgFile(null);
        setImgPreviewUrl('');
        setContent('');
        navigate(`/feedDetail/${id}`);
      } else {
        throw new Error('  .');
      }
    } catch (error) {
      console.error('Error submitting the form:', error);
      alert('  .');
    }
  };

  return (
    <div className={style.wrap}>
      <form className={style.createFeed} onSubmit={updateFeed}>
        <input
          type="text"
          placeholder=" "
          id="title"
          name="title"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
        <div className={style.imgdiv}>
          <label htmlFor="imgFile" className={style.uploadLabel}>
             
          </label>
          <input
            type="file"
            id="imgFile"
            name="imgFile"
            onChange={handleImageUpload}
            style={{ display: 'none' }}
          />
        </div>
        <div className={style.imgPreview}>
          {imgPreviewUrl ? (
            <img src={imgPreviewUrl} alt=" " />
          ) : (
            <span>  </span>
          )}
          {imgPreviewUrl && (
            <span className={style.deleteText} onClick={handleImageDelete}>
              x
            </span>
          )}
        </div>
        <input
          type="text"
          placeholder=" "
          id="content"
          name="content"
          value={content}
          onChange={(e) => setContent(e.target.value)}
        />
        <button type="submit"></button>
      </form>
    </div>
  );
};

export default FeedEdit;
