import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import style from '../../styles/feed/Feed.module.css';
import SearchBar from '../../components/feed/SearchBar';
import { useNavigate } from 'react-router-dom';

const Feed = () => {
  const [feedList, setFeedList] = useState([]);
  const [error, setError] = useState(null);
  const [searchKeyword, setSearchKeyword] = useState(''); //   
  const navigate = useNavigate(); // useNavigate  

  useEffect(() => {
    fetchFeeds(); //     
  }, []);

  const fetchFeeds = async () => {
    try {
      const response = await fetch('http://localhost:8080/feedList');
      if (!response.ok) {
        throw new Error('  ');
      }
      const data = await response.json();
      console.log('Fetched feeds:', data); //   
      setFeedList(data); //    
    } catch (error) {
      console.error('Error fetching feeds:', error);
      setError(error.message); //     
    }
  };

  const handleCreateFeedClick = () => {
    //   
    const isLoggedIn = localStorage.getItem('token') !== null; //  localStorage token    

    if (!isLoggedIn) {
      //      
      navigate('/login');
    } else {
      //    createFeed  
      navigate('/createFeed');
    }
  };

  //    
  const filteredFeeds = feedList.filter((feed) =>
    feed.title.toLowerCase().includes(searchKeyword.toLowerCase())
  );

  return (
    <div className={style.wrap}>
      <SearchBar onSearch={setSearchKeyword} />
      <div className={style.feed}>
        <div className={style.feedContainer}>
          {error ? (
            <p>: {error}</p> //   
          ) : filteredFeeds.length > 0 ? (
            filteredFeeds.map((feed) => (
              <Link
                to={`/feedDetail/${feed._id}`} //  url feed -> feedDetail 
                key={feed._id}
                className={style.feedImg}
              >
                <img src={feed.cover} alt={feed.title} />
              </Link>
            ))
          ) : (
            <p>  </p> //      
          )}
        </div>
      </div>
      <button className={style.createButton} onClick={handleCreateFeedClick}>
        <div className={style.plusIcon}></div>
      </button>
    </div>
  );
};

export default Feed;
